import { ElementRef, Renderer2 } from '@angular/core';
import { TiUploadRef } from '../../services/upload/TiFileInterface';
/**
 * 该指令适用于自定义上传场景，指令定义在input type='file'元素上，传入的是文件上传实例
 * 文件上传实例的生成，请参考[TiUploadService.create]{@link ../injectables/TiUploadService.html#create}
 *
 * 除自定义使用方式外，Tiny还提供了两种已进行设计的上传组件供业务使用，具体见[TiUploadComponent]{@link ../components/TiUploadComponent.html}
 *
 * <example-url>../tiny3demo/#/upload/upload-all</example-url>
 */
export declare class TiFileSelectDirective {
    private hostElementRef;
    private renderer;
    /**
     * 文件上传实例
     */
    tiFileSelect: TiUploadRef;
    private nativeElement;
    private fileChangeEvent;
    constructor(hostElementRef: ElementRef, renderer: Renderer2);
    /**
     * @ignore
     * 选择文件后，根据浏览器差异进行处理
     */
    onFileChange(fileSel: any): void;
    /**
     * @ignore
     * 替换单个文件选择按钮，确保后续文件选择可继续点选
     * 当前文件选择框
     * 文件选择实例对象
     */
    replaceFileInput(fileSel: any, isRemoveInput: boolean): void;
}
